<template>
<div id="edit-piloto">
    <h3>Edit Piloto</h3>
    <div class="row">
    <form @submit.prevent="updatePiloto" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="number" v-model="numero" required >
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="piloto" required>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="copiloto" required>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <div>
            <label class="typo__label">Clase</label>
            <multiselect v-model="clase" :options="options" :searchable="false" :close-on-select="true" :show-labels="false" placeholder="Elegir una Clase"></multiselect>
            </div>
        </div>
      </div>
       <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="asoc" required>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="equipo" required>
        </div>
      </div>
      
      
      <button type="submit" class="btn">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
    </form>
  </div>
</div>
    
</template>

<script>
  import db from './firebaseInit'
  import Multiselect from 'vue-multiselect'
  export default {
    components: {
        Multiselect
    },
    name: 'edit-piloto',
    data () {
      return {
            numero: null,
            piloto: null,
            copiloto: null,
            clase: '',
            asoc:null,
            equipo:null,
            options: ['RCN4', 'RCN-Nacional', 'R1B', 'R2B', 'R2B Libre']
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('pilotos').where('numero', '==', to.params.piloto_id).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
           vm.piloto = doc.data().piloto
            vm.copiloto = doc.data().copiloto
            vm.clase = doc.data().clase
            vm.equipo = doc.data().equipo
            vm.asoc = doc.data().asoc
            vm.numero = doc.data().numero
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('pilotos').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
           this.piloto = doc.data().piloto
            this.copiloto = doc.data().copiloto
            this.clase = doc.data().clase
            this.equipo = doc.data().equipo
            this.asoc = doc.data().asoc
            this.numero = doc.data().numero
          })
        })
      },
      updatePiloto () {
        db.collection('pilotos').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref.update({
            numero: this.numero,
                piloto: this.piloto,
                copiloto: this.copiloto,
                clase: this.clase,
                asoc: this.asoc,
                equipo: this.equipo
            })
            .then(() => {
              this.$router.push({ name: 'view-piloto', params: { piloto_id: this.numero }})
            });
          })
        })
      }
    }
  }
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>


