<template>
<div id="dashboard">
     <div>
         <h6>Buscar por Numero</h6>
        <form @submit.prevent="mandarnumero" class="col s12">
            <div class="input-field col s12">
          <input type="number" v-model="numero" required>
          <label>Numero#</label>
        </div>
        <button type="submit" class="btn">Buscar/Editar PE</button>
        </form>
    </div>

    <ul class="collection with-header">
        <li class="collection-header"><h4></h4>Pilotos</li>
        <li v-for="piloto in pilotos" v-bind:key="piloto.id" class="colection-item">
            <div class="chip">{{piloto.numero}}</div>{{piloto.piloto}}/{{piloto.copiloto}}/{{piloto.clase}}/{{piloto.asoc}}/{{piloto.equipo}}

            <router-link class="secundary-content" v-bind:to="{name:'view-piloto', params: { piloto_id:piloto.numero}}">
                <i class="fa fa-eye"></i>
            </router-link>

            <router-link class="secundary-content" v-bind:to="{name:'pruebaespecial', params: { piloto_id:piloto.numero}}">
                <i></i>
                PRUEBAS ESPECIALES
            </router-link>
        </li>
    </ul>

   
    <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
        </router-link>
    </div>
    

</div>
    
</template>
s
<script>
import db from "./firebaseInit"
export default {
    name:'dashboard',
    data (){
        return{
            pilotos : [],
            numero:null
        }
    },
      methods: {
           mandarnumero () {
                this.$router.push({name:'pruebaespecial', params: { piloto_id:this.numero}})
           }
      },
    created (){
        db.collection("pilotos").orderBy("numero").get().then
        (querySnapshot => {
            querySnapshot.forEach(doc =>{
                //console.log(doc.data())
                const data = {
                    "numero":doc.data().numero,
                    "piloto":doc.data().piloto,
                    "copiloto":doc.data().copiloto,
                    "clase":doc.data().clase,
                    "equipo":doc.data().equipo,
                    "asoc":doc.data().asoc,
                }
                this.pilotos.push(data)
            })

        })
    }

}
</script>



