<template>
<div id="view-piloto">
     <ul class="collection with-header">
      <li class="collection-header"><h4>Numero#: {{numero}}</h4></li>
      <li class="collection-item">Piloto: {{piloto}}</li>
      <li class="collection-item">Copiloto: {{copiloto}}</li>
       <li class="collection-item">Clase: {{clase}}</li>
        <li class="collection-item">Asociacion: {{asoc}}</li>
         <li class="collection-item">Equipo: {{equipo}}</li>
    </ul>
     <router-link to="/" class="btn grey">Back</router-link>
     <button @click="deletePiloto" class="btn red">Delete</button>

     <div class="fixed-action-btn">
      <router-link v-bind:to="{ name: 'edit-piloto', params: { piloto_id: numero }}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>

</div>
    
</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'view-piloto',
    data () {
      return {
        piloto: null,
        copiloto: null,
        clase: null,
        equipo: null,
        asoc: null,
        numero: null
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('pilotos').where('numero', '==', to.params.piloto_id).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.piloto = doc.data().piloto
            vm.copiloto = doc.data().copiloto
            vm.clase = doc.data().clase
            vm.equipo = doc.data().equipo
            vm.asoc = doc.data().asoc
            vm.numero = doc.data().numero
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('pilotos').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.piloto = doc.data().piloto
            this.copiloto = doc.data().copiloto
            this.clase = doc.data().clase
            this.equipo = doc.data().equipo
            this.asoc = doc.data().asoc
            this.numero = doc.data().numero
          })
        })
      },
      deletePiloto () {
          if(confirm ('Are you sure?')) {
               db.collection('pilotos').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE1
           db.collection('PE1').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE2
           db.collection('PE2').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE3
           db.collection('PE3').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE4
           db.collection('PE4').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE5
           db.collection('PE5').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
             // this.$router.push('/')
            })
            
          })

          //delete PE6
           db.collection('PE6').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

           //delete PE7
           db.collection('PE7').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE8
           db.collection('PE8').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE9
           db.collection('PE9').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

          //delete PE10
           db.collection('PE10').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              //this.$router.push('/')
            })
            
          })

           //delete GENERAL
           db.collection('GENERAL').where('numero', '==', this.$route.params.piloto_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push('/')
            })
            
          })

          

          

          
          }
      }
      
    }
  }
</script>


