import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewPiloto from '@/components/newpiloto'
import ViewPiloto from '@/components/viewpiloto'
import EditPiloto from '@/components/editpiloto'
import PruebaEspecial from '@/components/PruebaEspecial'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-piloto',
      component: NewPiloto
    },
    {
      path: '/edit/:piloto_id',
      name: 'edit-piloto',
      component: EditPiloto
    },
    {
      path: '/:piloto_id',
      name: 'view-piloto',
      component: ViewPiloto
    },
    {
      path: '/pruebaespecial/:piloto_id',
      name: 'pruebaespecial',
      component: PruebaEspecial
    }
  ]
})



